@import "../../../style/theme/default/var.css";

@component-namespace authority{
  @b tab{

  & .template-tree-organize.el-tree{
    background: var(--color-white);
  }
  }

  /* 面板 panel */
  @b panel{
    width: 208px;
    float:left;
    @e title{
      height: 30px;
      line-height: 30px;
      color: var(--theme-font-color-primary);
      font-size: var(--theme-font-size-base);
      b {
        color:var(--theme-color-secondary);
      }
    }

    @e body{
      .template-tree-organize.el-tree .el-tree-node .el-tree__extra-name .name-value{
        max-width: 90%;
      }
    }

    @when autosize{
      @utils-clearfix;
      width: auto;
      float: none;
      margin-left: 228px;
      .authority-panel__title,
      .authority-panel__body{
        float: left;
        width: 100%;
      }
    }
  }
  /* /end 面板 panel*/
/*  选择店铺列表 */
  @b shops{
    @e header{
      width: 100%;
      margin-bottom: var(--theme-margin-small);
      .el-checkbox{
        line-height: 30px;
      }
    }
    @e list{
    @utils-clearfix;
      .el-checkbox-group {
          .el-checkbox{
            float: left;
            width: 25%;
            padding-right:var(--theme-padding-middle);
            margin-bottom: var(--theme-margin-base);
           @utils-ellipsis;
          }
        .el-checkbox + .el-checkbox {
            margin-left: 0;
          }
      }
    }

  }
/* /end 选择店铺列表 */
  @b controls {
    text-align: center;
    margin-top: var(--theme-padding-base);
  }
  @b data{
   @utils-clearfix;
  }
  @b tabs {
      &.el-tabs {
      box-shadow:none;
      border:0;
      .el-tabs__header{
        background-color: var(--theme-background-color-secondary);
      }
      .el-tabs__nav{
        text-align: center;
        width: 100%;
      }
    .el-tabs__item{
      color: var(--color-white);
      @when active{
        color: var(--theme-color-primary);
      }
    }
    }
  }
}

@media (max-width: 1280px) {
  .authority-shops__list .el-checkbox-group .el-checkbox {
    width:50%;
  }
}



